    background-size: 23px;
  }

  .App .rightblock___content-send {
    padding-top: 40px;
    justify-content: center;
    gap: 40px;
  }

  .App .rightblock___content__header-send {
    padding-top: 0;
  }

  .App .rightblock__hand {
    left: -28%;
  }

  .App .left-block____answers {    
    padding-right: 10%;
  }

  .App .rightblock___content__text {
    font-size: 12px;
    line-height: normal;
    font-weight: 400;
  }

  .result div {
    padding-top: 75px;
  }

  
  .result .manDone {
    height: 70%;
  }

}


@media screen and (max-width: 960px) {

  .App .left-block {
    min-width: 56%;
  }

  .App .left-block___progress {
    height: 6px;
  }

  .App .left-block___list__item {
    height: 70px;
    font-size: 7px;
  }

  .App .left-block p {
    font-size: 12px;
  }

  .App .left-block____answers {
    margin-bottom: 30px;
  }
  
  .App .left-block____form___phone-error {
