}

@media screen and (max-width: 800px) {

  .App .rightblock {
    display: none;
  }

  .App .left-block___question-lable {
    font-size: 14px;
  }

  .App .left-block {
    width: 100%;
    padding-left: 40px;
    padding-right: 40px;
  }

  .App .left-block___list__item {
    height: 50px;
    font-size: 10px;
  }

  .App .left-block___list__item input + label::before {
    width: 13px;
    height: 13px;
  }

  .App .left-block___button {
    width: 28%;
    height: 40px;
  }

  .App .rightblock___man {
    width: 50%;
  }

  .App .rightblock___content__header {
    font-size: 14px;
    margin-bottom: 20px;
  }

  .App .rightblock___content__group_text {
